import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { JwtHelperService } from '@auth0/angular-jwt';
import swal from 'sweetalert';

const helper = new JwtHelperService();

@Injectable()
export class AuthGuard implements CanActivate {

    constructor( private rr : Router) { }

  canActivate() {
    //var token_var = localStorage.getItem('token');

    const isExpired = helper.isTokenExpired(localStorage.getItem('token'));

   // if (typeof token_var !== 'undefined' && token_var !== null && isExpired == false){
    if(isExpired==false){
      // console.log("si");

        return true;
    }else{

        if(isExpired == true){
            swal("Inicia sesion", "", "error");
        }
        //console.log("no");
        this.rr.navigateByUrl("/");
        return false;
        
       
    }

    // console.log('i am checking to see if you are logged in');
    // return true;
   
  }

 
}