import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Usuario } from '../model/usuario';
import { UsuarioService } from '../service/usuario.service';
import swal from 'sweetalert';


@Component({
  selector: 'app-usuario',
  templateUrl: './usuario.component.html',
  styleUrls: ['./usuario.component.css']
})
export class UsuarioComponent implements OnInit {

  public show:boolean = true;
  public idhere:string;

  usuario : Usuario = {
    nombre: "",
    correo: "",
    clave: "",
    estado: true
  }

 

  usuarios: Array<Usuario> = [];

  constructor(private _us : UsuarioService) { 

    this.listar();

  }

  ngOnInit() {

    

   

  }

  listar(){
    this._us.listar().subscribe((data)=>{
      this.usuarios = data.datos;
    }, err =>{
      console.log(err);
    });
  }

  guardar(){
    this._us.guardar(this.usuario).subscribe((data)=>{
      if(!data.ok){
        swal("Error" , "Error!", "warning");
      }else{
        //alert("se guardo");
        var heree = String(this.usuario.nombre);
        swal(heree , "Tu cuenta ha sido creada!", "success");
        this.listar();

        this.usuario = {
          nombre: "",
          correo: "",
          clave: "",
          estado: true
        }
      }
    },err =>{
      alert(err);
    })
  }

  editar(id){

    this._us.ver(id).subscribe((data)=>{
      
      //  console.log(data.datos);
        this.usuario = {
          nombre: data.datos.nombre,
          correo: data.datos.correo,
          clave: data.datos.clave,
          estado: data.datos.estado
        }

        this.idhere = id;

        

     
    }, err =>{
      alert(err);
    })
    
    window.scrollTo(0, 0);

   this.show = false;

  }

  modificar(){

    this._us.modificar(this.usuario, this.idhere).subscribe((data)=>{
      if(!data.ok){
        swal("Error" , "Error!", "warning");
      }else{
        swal("Guardado" , "El usuario ha sido editado!", "success");
        this.listar();

        this.usuario = {
          nombre: "",
          correo: "",
          clave: "",
          estado: true
        }
      }
    }, err =>{
      alert(err);
    })

    this.show = true;
    
  }

  elimnarr(id, estado1){
    let nuevoestado = estado1?false:true;
    this._us.eliminar(id, nuevoestado).subscribe((data)=>{
      this.listar();
      swal(nuevoestado?"Activado":"Eliminado" , "", nuevoestado?"success":"warning");
   
  }, err =>{
    alert(err);
  })
  }
}
