import { Component, OnInit, NgZone, ViewEncapsulation } from '@angular/core';
import { UsuarioService } from '../service/usuario.service';
import { Router } from '@angular/router';
import swal from 'sweetalert';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class LoginComponent implements OnInit {

usuario = {
  email: "",
  clave: ""
}

  constructor(private _su : UsuarioService, private r : Router, private _ngZone: NgZone) { }

  ngOnInit() {
  }

  ingresar(){
    this._su.login(this.usuario).subscribe((data)=>{

      if(data.ok){
        localStorage.setItem("token", data.token);
        
       // this.r.navigate(["usuarioreactivo"]);
       this._ngZone.run(() => this.r.navigateByUrl("usuarioreactivo"))
      }else{
        // alert(data.men);
        swal(data.men);
      }

    }, err =>{
      //alert(err.error.men);
      // swal(err.error.men);
      swal(err.error.men, "", "error");
    });
  }

}
