import { Router, Routes } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { UsuarioComponent } from './usuario/usuario.component';
import { MascotaComponent } from './mascota/mascota.component';
import { UsuarioReactivoComponent } from './usuario-reactivo/usuario-reactivo.component';
import { AuthGuard } from './auth-guard.service';

export const appRoutes: Routes = [
    { path: 'usuarios', canActivate: [AuthGuard], component:UsuarioComponent  },
    { path: 'mascotas', canActivate:[AuthGuard], component:MascotaComponent  },
    { path: 'usuarioreactivo', canActivate:[AuthGuard], component:UsuarioReactivoComponent  },
    { path: '',      component: LoginComponent },
    { path: '**', component: LoginComponent }
  ];