import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Mascota } from '../model/mascota';
import { MascotaService } from '../service/mascota.service'
import swal from 'sweetalert';

@Component({
  selector: 'app-mascota',
  templateUrl: './mascota.component.html',
  styleUrls: ['./mascota.component.css']
})
export class MascotaComponent implements OnInit {

  public show:boolean = true;
  public idhere:string;

  // mascota : Mascota ={
  //   nombre: "",
  //   tipoMascota: "",
  //   raza: "",
  //   edad: "",
  //   ubicacion: "",
  //   descripcion: "",
  //   estado: true
  // }

  formMascota = this.formBuilder.group({
    nombre:['', Validators.required],
    tipoMascota:['', Validators.required],
    raza:['', Validators.required],
    edad:['', Validators.required],
    ubicacion:['', Validators.required],
    descripcion:['', Validators.required],
    fotos:[''],
    published_by:[''],
    estado:[true]
    
  });

  mascotas: Array<Mascota> =[];

  constructor(private _us : MascotaService, private formBuilder : FormBuilder) { }

  ngOnInit() {
    this.listar();
  }

  listar(){
    
    this._us.listar().subscribe((data)=>{
      this.mascotas = data.datos;
    }, err =>{
     
    });
    
  }


  guardar(){

    const arrayhere = ["d9qojsby2fbmsqznogb6", "tbn5shmhsdvulziiic5x", "czfqcycaxe0jsxb4v14n", "hy86b7mnx6uwdsta6kbw"];

    this.formMascota.patchValue({fotos: arrayhere });
    this._us.guardar(this.formMascota.value).subscribe((data)=>{
      if(!data.ok){
      //  alert("error");
      swal("Error" , "Error!", "warning");
      }else{
        //alert("Se guardo Mascota");
        var heree = String(this.formMascota.value.nombre);
        swal(heree , "Mascota creada!", "success");
        this.listar();

        this.formMascota.reset();

      }
    }, err =>{
      alert(err);
    })
  }

  editar(id){

    this._us.ver(id).subscribe((data)=>{
      
      //  console.log(data.datos);
        this.formMascota.patchValue( {
          nombre: data.datos.nombre,
          tipoMascota: data.datos.tipoMascota,
          raza: data.datos.raza,
          edad: data.datos.edad,
          ubicacion: data.datos.ubicacion,
          descripcion: data.datos.descripcion,
          estado: data.datos.estado
        });

        this.idhere = id;

     
    }, err =>{
      alert(err);
    })
    
    window.scrollTo(0, 0);

   this.show = false;

  }

  modificar(){

    this._us.modificar(this.formMascota.value, this.idhere).subscribe((data)=>{
      if(!data.ok){
      //  alert("error");
      swal("Error" , "Error!", "warning");
      }else{
        //alert("Mascota Editada");
        swal("Guardado" , "El usuario ha sido editado!", "success");
        this.listar();

        this.formMascota.reset();
      }
    }, err =>{
      alert(err);
    })

    this.show = true;
    
  }

  elimnarr(id){
    this._us.ver(id).subscribe((data)=>{
      

      let nuevoestado = data.datos.estado?false:true;
      this._us.eliminar(id, nuevoestado).subscribe((data)=>{
      this.listar();
      this.formMascota.reset();
      swal(nuevoestado?"Activado":"Eliminado" , "", nuevoestado?"success":"warning");
   
      }), err =>{
        alert(err);
      }

        

     
    }, err =>{
      alert(err);
    })
  }


}
