import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Usuario } from '../model/usuario';
import { UsuarioService } from '../service/usuario.service';
import swal from 'sweetalert';

@Component({
  selector: 'app-usuario-reactivo',
  templateUrl: './usuario-reactivo.component.html',
  styleUrls: ['./usuario-reactivo.component.css']
})
export class UsuarioReactivoComponent implements OnInit {

  public show:boolean = true;
  public idhere:string;

  usuarios: Array<Usuario> = [];

  formUsuario = this.formBuilder.group({
    nombre:['', Validators.required],
    correo:['',  [Validators.required, Validators.email]],
    clave:['', [Validators.required, Validators.minLength(7)]],
    estado:[true],
    tipodeusuario:['usuario', Validators.required],
    telefono:[''],
    acercade:['', Validators.required],
    ubicacion:['', Validators.required]
    
  });

  

  constructor(private _us : UsuarioService, private formBuilder : FormBuilder ) {
    this.listar();
   }

  ngOnInit() {
  }

  listar(){
    this._us.listar().subscribe((data)=>{
      this.usuarios = data.datos;
    }, err =>{
      console.log(err);
    });
  }

  guardar(){
    
    this._us.guardar(this.formUsuario.value).subscribe((data)=>{
      
      if(!data.ok){
        //console.log(data.err.errors.correo.message);
       swal("Error" , data.err.message, "warning");
      }else{
        //alert("se guardo");
        var heree = String(this.formUsuario.value.nombre);
        swal(heree , "Tu cuenta ha sido creada!", "success");
        this.listar();

        this.formUsuario.reset();
      }
    }, err =>{
     
      alert(err);
 
     })
  }

  editar(id){

    this._us.ver(id).subscribe((data)=>{
      
      //  console.log(data.datos);
        // this.usuario = {
        //   nombre: data.datos.nombre,
        //   correo: data.datos.correo,
        //   clave: data.datos.clave,
        //   estado: data.datos.estado
        // }

        this.formUsuario.patchValue({
          nombre: data.datos.nombre,
          correo:  data.datos.correo,
          // clave: data.datos.clave,
          estado: data.datos.estado,
          tipodeusuario: data.datos.tipodeusuario,
          telefono: data.datos.telefono,
          acercade: data.datos.acercade,
          ubicacion: data.datos.ubicacion,
        });

        this.idhere = id;

        

     
    }, err =>{
      alert(err);
    })
    
    window.scrollTo(0, 0);

   this.show = false;

  }

  modificar(){

    this._us.modificar(this.formUsuario.value, this.idhere).subscribe((data)=>{
      if(!data.ok){
       // swal("Error" , "Error!", "warning");
       swal("Error" , data.err.message, "warning");
       
      }else{
        swal("Guardado" , "El usuario ha sido editado!", "success");
        this.listar();

        //this.formUsuario.reset();
      }
    }, err =>{
      console.log(err.error.err.message);
    })

    this.formUsuario.reset();
    this.show = true;
    
  }

  elimnarr(id){

    this._us.ver(id).subscribe((data)=>{
      

      let nuevoestado = data.datos.estado?false:true;
      this._us.eliminar(id, nuevoestado).subscribe((data)=>{
      this.listar();
      this.formUsuario.reset();
      swal(nuevoestado?"Activado":"Eliminado" , "", nuevoestado?"success":"warning");
   
      }, err =>{
        alert(err);
      })

        

     
    }), err =>{
      alert(err);
    }

  }

  cancelar(){
    this.formUsuario.reset();
    this.show = true;
  }

}
